/* import '../node_modules/font-awesome/css/font-awesome.min.css';  */

.App {
  font-family: sans-serif;
  text-align: center;
  width: 30%;
  margin: auto;
}

body {
  background-color: #cecece;
  color: black;
}
button {
  border: none;
  cursor: pointer;
}

.inputbox {
  width: 100%;
  padding: 20px 35px;
  padding-right: 5px;
  background-color: #fff;
  border-radius: 20px;
  outline: none;
  border: none;
  margin-right: 5px;
  font-size: 1.5625em;
}

::placeholder {
  font-weight: 500;
  color: black;
}

.addtaskbtn {
  background-color: #fff;
  border-radius: 20px;
  padding: 25px;
  margin-left: 5px;
}

.radiobtn {
  padding: 10px;
  border-radius: 50%;
  background: #e8e8e8;
  /* outline: 8px solid #87fcc5; */
  margin: auto 25px;
}
.task > div {
  display: flex;
  align-items: center;
}
.task > div > span,
.taskstatustick,
img {
  cursor: pointer;
}
.task {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 20px 35px;
  border-radius: 20px;
  font-size: 1.5625rem;
  margin: auto;
  margin-top: 10px;
}
.taskinput {
  display: flex;
  justify-content: space-between;
  margin: auto;
  margin-top: 30px;
}

@media screen and (max-width: 1130px) {
  .App {
    width: 50%;
  }
}
@media screen and (max-width: 860px) {
  .App {
    width: 70%;
  }
}
@media screen and (max-width: 500px) {
  .App {
    width: 90%;
  }
}
